.map_container {
  width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 30px;
  text-align: right;
  text-transform: capitalize;
  margin-top: 70px;
}

.map {
  width: 100%;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
}
